.lesson-page {
    padding: 20px;
  }
  
  .lesson-image {
    width: 100%;
    height: auto;
  }
  
  .lesson-content {
    margin-top: 20px;
  }
  .lesson-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent background */
    color: white;
    padding: 20px;
    box-sizing: border-box;
    overflow: auto;
    z-index: 1000; /* Ensure it's on top of other content */
  }
  
  .lesson-overlay img {
    max-width: 100%;
    height: auto;
  }
  
  .lesson-overlay button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #f44336; /* Red color */
    border: none;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .lesson-overlay button:hover {
    background-color: #c62828; /* Darker red */
  }
  
  .correct {
    background-color: lightgreen !important;
  }
  .endpoint {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background: linear-gradient(135deg, #f7f9fc, #e0e7ff);
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.endpoint h2 {
    font-size: 28px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    border-bottom: 2px solid #e0e7ff;
    padding-bottom: 10px;
}

.endpoint p {
    font-size: 18px;
    color: #555;
    margin: 15px 0;
    line-height: 1.6;
}

.endpoint button {
    background: linear-gradient(135deg, #6a8dff, #4a6aff);
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    padding: 12px 25px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-transform: uppercase;
    letter-spacing: 1px;
}

.endpoint button:hover {
    background: linear-gradient(135deg, #4a6aff, #2a4aff);
    transform: translateY(-2px);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.endpoint button:active {
    transform: translateY(0);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}
